#nav {
  position: fixed;
  width: 25%;
  top: 0;
  bottom: 0;
  left: 0;
  height: 100vh;
  z-index: 5;
  padding-left: 2rem;
}

#nav ul {
  display: flex;
  flex-wrap: wrap;
  height: 100%;
}

#nav a {
  color: #9C27B0;
}

#fullpage .section {
  width: 100vw;
}

#fullpage .section-background {
  background-size: cover;
  background-position: center bottom;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

#fullpage .container-fluid {
  height: 100vh;
}

#fullpage .container-fluid > .row {
  height: 100%;
  width: 100%;
}

#fullpage .bottom-5 {
  padding-bottom: 5rem;
}

#fullpage .top-3 {
  padding-top: 3rem;
}

#fullpage a {
  display: inline-block;
  color: #FFC107;
  text-decoration: none;
  padding-bottom: 1px;
  margin-bottom: .3rem;
  cursor: pointer;
  transition: color .3s;
}

#fullpage a:hover {
  color: #9C27B0;
}

#fullpage hr {
  border-top: 0;
  border-right: 0;
  border-left: 0;
  border-bottom-width: .15rem;
}

#fullpage address ul {
  margin: 0 1rem;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

#fullpage address li {
  padding: 0 .5rem;
}

#fullpage .screen {
  background: linear-gradient(to top, rgba(0, 0, 0, 0.3) 10%, rgba(0, 0, 0, 0.1));
  border-radius: 2px;
}

#fullpage .title {
  color: white;
  font-size: 3rem;
}

#fullpage .title-sub {
  color: white;
  font-size: 1.6rem;
}

#fp-nav ul li {
  margin: 1.1rem .5rem;
  padding: 0.3rem 0px;
  width: auto;
  height: auto;
}

#fp-nav ul li a span {
  background: #9C27B0;
  height: .5rem;
  width: .5rem;
  margin: -.25rem 0 0 -.25rem;
}

#fp-nav ul li a.active span {
  height: 1rem;
  width: 1rem;
  margin: -.5rem 0 0 -.5rem;
}

#fp-nav ul li .fp-tooltip {
  color: #9C27B0;
}

