#fullpage {
  .section { width: 100vw; }
  .section-background {
    background-size: cover;
    background-position: center bottom;
    background-repeat: no-repeat;
    background-attachment: fixed;
  }
  .container-fluid {
    height: 100vh;
    & > .row {
      height: 100%;
      width: 100%;
    }
  }
  .bottom-5 { padding-bottom: 5rem; }
  .top-3 {padding-top: 3rem;}
  a {
    display: inline-block;
    color: $amber;
    text-decoration: none;
    padding-bottom: 1px;
    margin-bottom: .3rem;
    cursor: pointer;

    transition: color .3s;
    &:hover { color: $purple-hover;}
  }
  hr {
    border-top: 0;
    border-right: 0;
    border-left: 0;
    border-bottom-width: .15rem;
  }
  address {
    ul {
      margin: 0 1rem;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;
    }
    li { padding: 0 .5rem}
  }
  .screen {
    background: linear-gradient(to top, rgba(black, .3) 10%, rgba(black, .1));
    border-radius: 2px;
  }
  .title {
    color: white;
    font-size: 3rem;
    &-sub {color: white; font-size: 1.6rem}
  }
}
// nav
#fp-nav ul li {
  margin: 1.1rem .5rem;
  padding: 0.3rem 0px;
  width: auto;
  height: auto;
  a {
    span {
      background: $purple-hover;
      height: .5rem;
      width: .5rem;
      margin: -.25rem 0 0 -.25rem;
    }
    &.active {
      span {
        height: 1rem;
        width: 1rem;
        margin: -.5rem 0 0 -.5rem;
      }
    }
  }
}
#fp-nav ul li .fp-tooltip { color: $purple-hover;}
